
 /**
 * *
 * @param {*id} uid
 * @returns bool
 */
export const hasButtonPermission=uid=>{
    if(localStorage.getItem('leftMenuList')){
        let buttonList=JSON.parse(localStorage.getItem('leftMenuList')).menus
        buttonList=buttonList.reduce((prev,current)=>{
            return prev.concat(current.subMenus.map(inner=>{
                if(inner.type === 2){
                    return inner
                }else{
                    if(inner.subMenus && inner.subMenus.length>0){
                        return inner.subMenus.map(i=>{
                            if(i.type === 2){
                                return i
                            }else{
                                return null
                            }
                        })
                    }else{
                        return null
                    }
                }
            }))
        },[]).filter(item=>item)
        buttonList=Array.prototype.concat.apply([],buttonList)
        let item=buttonList.find(item=>item.id === uid)
        if(item){
            return true
        }else{
            return false
        }
    }else{
        return false
    }
}

/**
 * 
 * @param {} funcwait
 * @returns void
 */
export const debounds = (func,wait) => {
    let timeOut = null;
    return function (...args) {
        clearTimeout(timeOut);//
        timeOut = setTimeout(() => {
        func(...args);
        }, wait);
    };
}
